import "core-js/modules/es.array.push.js";
import fetchWithAuth from '@/utils/fetchWithAuth';
import DashboardTabs from '@/components/DashboardTabs.vue';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      tableData: [],
      dealedData: [],
      // 根据IP地址处理后的数据，格式为 {ip, count, [data]}
      blockipList: [],
      // 禁止上传的IP列表

      loading: false,
      // 分页数据
      currentPage: 1,
      pageSize: 10 // 默认每页10条
    };
  },
  components: {
    DashboardTabs
  },
  computed: {
    disableTooltip() {
      return window.innerWidth < 768;
    },
    paginatedData() {
      // 计算分页数据
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.dealedData.slice(start, end);
    }
  },
  methods: {
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    formatTimeStamp(timeStamp) {
      return new Date(timeStamp).toLocaleString();
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    },
    async handleSwitchEnable(row) {
      const ip = row.ip;
      const enable = row.enable;
      if (enable) {
        // 从 blockipList 中移除
        this.blockipList = this.blockipList.filter(item => item !== ip);
        // 更新 blockipList
        await fetchWithAuth("/api/manage/cusConfig/whiteip", {
          method: 'POST',
          body: ip
        });
      } else {
        // 添加到 blockipList 中
        this.blockipList.push(ip);
        // 更新 blockipList
        await fetchWithAuth("/api/manage/cusConfig/blockip", {
          method: 'POST',
          body: ip
        });
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
      // 到最后一页时，加载更多数据
      if (page === Math.ceil(this.dealedData.length / this.pageSize)) {
        this.loadMoreData();
      }
    },
    loadMoreData() {
      this.loading = true;
      const start = this.dealedData.length;
      const count = 20; // 每次加载20条数据
      fetchWithAuth(`/api/manage/cusConfig/list?start=${start}&count=${count}`, {
        method: 'GET'
      }).then(response => response.json()).then(result => {
        this.dealedData = this.dealedData.concat(result.map(item => {
          const enable = !this.blockipList.includes(item.ip);
          return {
            ip: item.ip,
            address: item.address,
            count: item.count,
            data: item.data,
            enable: enable
          };
        }));
      }).catch(() => {
        this.$message.error('加载更多数据时出错，请检查网络连接');
      }).finally(() => {
        this.loading = false;
      });
    },
    handleSizeChange(size) {
      this.pageSize = size;
      this.currentPage = 1;
    }
  },
  mounted() {
    this.loading = true;
    fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // 在 check 成功后再执行 list 的 fetch 请求
        return fetchWithAuth("/api/manage/cusConfig/list?count=20", {
          method: 'GET'
        });
      } else if (result == "Not using basic auth.") {
        return fetchWithAuth("/api/manage/cusConfig/list?count=20", {
          method: 'GET'
        });
      } else {
        throw new Error('Unauthorized');
      }
    }).then(response => response.json()).then(async result => {
      // 读取blockipList, 接口返回格式为 'ip1,ip2,ip3'，需要转换为数组
      const blockipList = await fetchWithAuth("/api/manage/cusConfig/blockipList", {
        method: 'GET'
      });
      this.blockipList = (await blockipList.text()).split(',');
      this.dealedData = result.map(item => {
        const enable = !this.blockipList.includes(item.ip);
        return {
          ip: item.ip,
          address: item.address,
          count: item.count,
          data: item.data,
          enable: enable
        };
      });
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('同步数据时出错，请检查网络连接');
      }
    }).finally(() => {
      this.loading = false;
    });
  }
};